@mixin input {
  height: 40px;
  border: none;
  outline: none;
  border-radius: 8px;
  background-color: #fff;
  margin-top: 4px;
}

.new-product--container {
  margin-top: 26px;
  margin-left: 33px;
  margin: 26px 33px 0;
}

.new-product {

  &__dropdown-menu {
    width: 100%;
    position: relative;
    top: 100%;
    left: 0;
    z-index: 30;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;

    .new-product__dropdown-items {
      box-sizing: border-box;
      width: inherit !important;
      padding: 6px;
      background-color: #fff;
      cursor: pointer;
      color: #000;

      &:hover {
        background: rgba(239, 244, 255, 1);
        // background: red;
      }

      &:first-child {
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
      }

      &:last-child {
        border-bottom-left-radius: 6px;
        border-bottom-right-radius: 6px;
      }

      .new-product__dropdown-item {
        width: inherit !important;
      }
    }
  }

  &__drop-down--category {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }

  &__main {
    display: flex;
    gap: 28px;
  }

  &__left-side {
    width: 355px;
  }

  &__right-side {
    box-sizing: border-box;
    padding-top: 31px;

    &--first-group {
      display: flex;
      gap: 25px;

      &--input {
        width: 482px !important;
        margin-top: 4px;
      }
    }

    &--second-group {
      display: flex;
      gap: 25px;

      &--inputs {
        display: flex;
        margin-top: 4px;



        & :nth-child(n + 1) {
          width: 128px;
          margin-right: 8px;
        }

        & :last-child {
          width: 74px;
          margin: 0;
        }

        &.volume {
          display: flex;

          & :first-child {
            width: 141px;
          }

          & :last-child {
            width: 80px;
            position: relative;
          }
        }

        &.weight {
          & input:first-child {
            width: 156px;
          }
        }
      }
    }

    &--third-group {

      &-textarea {
        display: block;
        border: none;
        resize: none;
        outline: none;

        margin-top: 4px;

        box-sizing: border-box;

        padding: 15px;

        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;

        color: #000000;

        width: 979px;
        height: 245px;
        border-radius: 8px;

        margin-bottom: 12px;
      }
    }

    &--fourth-group {
      margin-bottom: 12px;

      &-textarea {
        display: block;
        border: none;
        resize: none;
        outline: none;

        margin-top: 4px;

        box-sizing: border-box;

        padding: 15px;

        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;

        color: #000000;

        width: 979px;
        height: 116px;
        border-radius: 8px;
      }
    }

    &--fifth-group {
      display: flex;
      align-items: center;
      gap: 24px;

      &--flex {
        display: flex;
        align-items: center;

        & :first-child {
          margin-right: 6px;
        }
      }

      & .new-product__input {

        &--first {
          width: 233px;
        }

        &--second {
          width: 235px;
        }

        &--third {
          width: 145px;
        }

        &--fourth {
          margin-left: 8px;
          width: 80px;
        }
      }
    }

    &--sixth-group {
      display: flex;
      flex-wrap: wrap;

      gap: 20px;

      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;

      background: linear-gradient(180deg, #5A8FFF 0%, #003094 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;

      margin-top: 47px;
      margin-bottom: 18px;
    }

    &--seventh-group {
      display: flex;
      gap: 12px;
      align-items: flex-end;

      &-button {
        display: block;
        height: 40px;

        position: relative;

        box-sizing: border-box;

        border: none;

        padding: 10px 44px;
        white-space: nowrap;

        &:before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          border: 1px solid transparent;
          background: linear-gradient(360deg, #5A8FFF 0%, #003094 100%);
          border-radius: 8px;
          -webkit-mask:
            linear-gradient(#fff 0 0) padding-box,
            linear-gradient(#fff 0 0);
          -webkit-mask-composite: destination-out;
          mask-composite: exclude;
        }

        .button-text {
          font-family: 'Montserrat';
          font-style: normal;
          font-weight: 300;
          font-size: 16px;
          line-height: 20px;

          background: linear-gradient(180deg, #0029FF 0%, #001A77 100%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          background-clip: text;
          text-fill-color: transparent;

          &--save {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 300;
            font-size: 16px;
            line-height: 20px;
            color: #fff;
          }
        }

        &--litle {
          width: 113px;
          height: 28px;
          padding: 6px 34px;

          .button-text {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 15px;
          }
        }

        &--big {
          padding: 10px 71px;
        }

        &--save {
          background: linear-gradient(180deg, #5A8FFF 0%, #003094 100%);
          border-radius: 8px;
          z-index: -1;

          &::before {
            content: none;
          }
        }
      }
    }
  }

  &__form {
    color: #909090;

    &--first-input {
      margin-bottom: 6px;
    }

    &--fourth-group {
      display: flex;
      gap: 24px;
      margin-bottom: 12px;

      &-inputs {
        display: flex;
        gap: 8px;
        margin-top: 4px;

        &-input {
          width: 78px;
          height: 40px;
          background: #FFFFFF;
          border: none;
          border-radius: 8px;
          outline: none;
        }
      }
    }

    &--fifth-group {
      display: flex;
      gap: 24px;
      margin-bottom: 27px;

      &-input {
        @include input;
      }
    }

    &--sixth-group {
      display: flex;
      align-items: center;
      gap: 8px;

      color: #000;



      &-switcher {
        display: block;
        width: 40px;
        height: 21px;

        position: relative;
        background: #B4B4B4;
        border-radius: 100px;

        transition: all 0.2s;

        &::before {
          transition: all 0.2s;
          position: absolute;
          content: '';
          display: block;
          width: 17px;
          height: 17px;

          top: 50%;
          left: 2px;
          transform: translateY(-50%);

          background: #F0F0F0;
          border-radius: 100%;
          box-shadow: inset 1px -1px 2px rgba(198, 198, 198, 0.2), inset -1px 1px 2px rgba(198, 198, 198, 0.2), inset 1px 1px 2px rgba(255, 255, 255, 0.9), inset -1px -1px 3px rgba(198, 198, 198, 0.9);
        }
      }

      &-input {
        display: none;

        &:checked~.new-product__form--sixth-group-switcher {
          position: relative;
          background: linear-gradient(180deg, #5A8FFF 0%, #003094 100%);


          &::before {
            position: absolute;
            content: '';
            display: block;
            width: 17px;
            height: 17px;

            left: 75%;
            transform: translate(-50%, -50%);

            background: #F0F0F0;
            border-radius: 100%;
            box-shadow: inset 1px -1px 2px rgba(198, 198, 198, 0.2), inset -1px 1px 2px rgba(198, 198, 198, 0.2), inset 1px 1px 2px rgba(255, 255, 255, 0.9), inset -1px -1px 3px rgba(198, 198, 198, 0.9);
          }
        }
      }
    }
  }

  &__input {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    outline: none;
    border: none;
    background: #FFFFFF;
    border-radius: 8px;
    height: 40px;
    display: inline-block;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;

    margin-bottom: 12px;
  }

  &__top-actions {
    display: flex;
    justify-content: flex-end;
    gap: 75px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;

    &--text {
      &-active {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        position: relative;

        &::after {
          display: block;
          content: '';

          position: absolute;
          bottom: -11px;

          width: 100%;
          height: 1px;

          border-radius: 2px;
          background-color: #5A8FFF;
        }
      }
    }
  }

  &__title {
    display: flex;
    align-items: center;
    gap: 28px;
    margin-bottom: 16px;

    &-main {
      margin: 0;
    }
  }

  &__product-id {
    &--text {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      margin-right: 4px;
      color: #919090;
    }

    &--id {
      color: #000;
      font-weight: 500;
      margin-right: 6px;
    }
  }

  &__add-photo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #FFFFFF;
    border-radius: 8px;
    width: 100%;
    height: 326px;
    margin-bottom: 15px;
    position: relative;

    &--actions {
      display: flex;
      gap: 161px;
      position: absolute;
      bottom: 15px;

      &-item {
        box-sizing: border-box;
        padding: 6px 12px;
        position: relative;

        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;
        background: #EFF4FF;
        border-radius: 6px;

        &-text {
          background: linear-gradient(180deg, #5A8FFF 0%, #003094 100%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          background-clip: text;
          text-fill-color: transparent;
          z-index: 10;
        }
      }
    }

    img {
      width: 150px;
    }

    &--text {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 300;
      font-size: 12px;

      background: linear-gradient(180deg, #5A8FFF 0%, #003094 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;

      margin-top: 8px;

      #file {
        display: none;
      }
    }
  }

  &__table {
    display: block;
    width: 100%;
    margin-top: 15px;

    &-head {
      display: grid;
      gap: 20px;
      width: 99%;
      position: sticky;
      top: 0px;
      z-index: 20;

      &--tr {
        display: flex;
        width: 100%;
        gap: 18px;

        justify-content: space-between;
        align-items: center;

        box-sizing: border-box;
        position: relative;
        padding: 14px !important;

        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;

        &::before {
          position: absolute;
          display: block;
          content: '';

          z-index: -1;

          top: 0;
          left: 0;

          width: 100%;
          height: 100%;

          background: #E5EAF5;
          border-radius: 8px;
        }
      }

      &--th {
        display: flex;
        align-items: center;
        gap: 4px;
        width: min-content;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;
      }

    }

    &-body {
      display: grid;
      width: 99%;

      &--tr {
        display: flex;
        width: 100%;
        gap: 18px;

        margin-top: 12px;

        justify-content: space-between;
        align-items: center;

        box-sizing: border-box;
        position: relative;
        padding: 14px !important;

        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;

        &::before {
          position: absolute;
          display: block;
          content: '';

          z-index: -1;

          top: 0;
          left: 0;

          width: 100%;
          height: 100%;

          background: #fff;
          border-radius: 8px;
        }
      }

      &--td {
        display: flex;
        align-items: center;
        gap: 4px;
        width: min-content;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;
      }
    }
  }
}

.labelIcon {
  margin-right: 8px;
}

.drop-down__position-relative {
  position: relative;
}

.drop-down-icon {
  position: absolute;
  right: 14px;
  top: 50%;
  transform: translateY(-50%);
}

sup {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 8px;
  line-height: 10px;

  position: relative;

  top: -4px;
}