@font-face {
  font-family: 'Montserrat-bold';
  src: url('./fonts/montserrat/montserrat-bold.woff');
}

;

@font-face {
  font-family: 'Montserrat-light';
  src: url('./fonts/montserrat/montserrat-light.woff');
}

;

@font-face {
  font-family: 'Montserrat-medium';
  src: url('./fonts/montserrat/montserrat-medium.woff');
}

;

@font-face {
  font-family: 'Montserrat-regular';
  src: url('./fonts/montserrat/montserrat-regular.woff');
}

;

@font-face {
  font-family: 'Montserrat-semibold';
  src: url('./fonts/montserrat/montserrat-semibold.woff');
}

body {
  background-color: #f4f4f4;
  font-family: 'Montserrat', sans-serif;
  margin: 0;
}

.products {
  display: flex;
  margin: 26px 25px 17px 24px;

  &__menu {
    width: 383px;
    height: 830px;
    margin-right: 24px;
  }
}

.fields {
  overflow: hidden;
}

svg {
  cursor: pointer;
}

.left-sidebar {
  display: flex;
  flex-direction: column;
  align-items: center;

  box-sizing: border-box;
  width: 77px;
  background: linear-gradient(180deg, #5A8FFF 0%, #003094 100%);
  height: 100vh;
  padding-top: 14px;
  padding-bottom: 43px;

  &__list {
    display: flex;
    height: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;

    gap: 27px;
  }
}

.right-sidebar {
  box-sizing: border-box;
  padding-top: 21px;
  width: 75px;
  background: #F0F0F0;

  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;

    margin: 0;
    padding: 0;
    list-style: none;
  }
}

.main {
  display: grid;
  grid-template-columns: 77px 1fr 75px;
  justify-content: space-between;
}

